body {
  margin: 0;
}

.product-card {
  font-family: Roboto, sans-serif;
  --product-card-width: 200px;
  --product-card-border: 1px;
  --product-card-padding: 16px;
  --image-margin-bottom: 40px;
  --title-margin-bottom: 4px;
  ---code-margin-bottom: 17px;
  ---rate-margin-bottom: 24px;
  --each-star-margin-rigth: 4px;
  --each-star-height: 16px;
  --each-star-width: 16px;
  --price-margin-bottom: 15px;
  width: calc(var(--product-card-width)
  - ((var(--product-card-border)
  + var(--product-card-padding)) * 2));
  padding: var(--product-card-padding);
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  background-color: #fff;
  flex-direction: column;
  color: #060b35;
}

.product-card .product-img {
  content: url("/images/imac.jpeg");
  height: 134px;
  width: 160px;
  display: block;
  margin: calc(32px - var(--product-card-padding)) auto
    var(--image-margin-bottom) auto;
}

.product-card .title {
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  margin-bottom: var(--title-margin-bottom);
}

.product-card .product-code {
  font-size: 10px;
  font-weight: 400;
  color: #616070;
  margin-bottom: var(---code-margin-bottom);
}

.rating-block {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: 400;
  font-size: 10px;
  margin-bottom: var(---rate-margin-bottom);
}

.product-card .rating-block .stars {
  display: flex;
}

.product-card .rating-block .stars__star {
  height: var(--each-star-height);
  width: var(--each-star-width);
  background-image: url("./images/star.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.product-card .rating-block .stars__star:not(:last-child) {
  margin-right: var(--each-star-margin-rigth);
}

.product-card .rating-block .stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("./images/star-active.svg");
}

.product-card .price-block {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: var(--price-margin-bottom);
}

.product-card .price-block .price-word {
  font-size: 12px;
  font-weight: 400;
  color: #616070;
}

.product-card .price-block .price-value {
  font-size: 16px;
  font-weight: 700;
}

.product-card .purchase-block {
  display: flex;
  justify-content: center;
  padding: calc(12px - 1px) 0;
  width: calc(100% - 1px);
  background: #00acdc;
  border-radius: 5px;
  font-size: 14px;
  text-transform: uppercase;
  color: #fff;
  font-weight: 700;
  text-decoration: none;
}

.product-card .purchase-block:hover {
  color: #00acdc;
  background-color: #fff;
  border: 0.5px solid #00acdc;
}
